#head{
background:red;
color:white;
height:70px;
display:flex;
justify-content: space-around;
}
.flexcenter{
  align-items: center;
  display:flex;
 width:100%;
  height:100%;
  text-align:center;
}
#video{
  display:grid;
  grid-template-columns: auto;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width:900px) {
  #video{grid-template-columns: auto }  
  }

iframe{
  margin-right: auto;
  margin-left: 5%;
  width:90%;
min-height: 60vh;

}

